import React from "react";
import s from './Avatar.module.css';
import Preloader from "../../../common/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const Avatar = ({profile, updateStatus, status}) => {

    if (!profile) {
        return <Preloader/>
    } else {
        return (
            <div className={s.content}>
                {/*<img src={props.profile.photos.large}/>*/}
                <div className={s.ava}>
                    <img src={profile.photos.small}/>
                    {profile.fullName}
                </div>
                Status: <ProfileStatusWithHooks updateStatus={updateStatus} status={status}/>
                <span>About me: {profile.aboutMe}</span>
                <ul>
                    Contacts:
                    <li>
                        facebook: {profile.contacts.facebook}
                    </li>
                    <li>
                        website: {profile.contacts.website}
                    </li>
                    <li>
                        vk: {profile.contacts.vk}
                    </li>
                    <li>
                        twitter: {profile.contacts.twitter}
                    </li>
                    <li>
                        instagam: {profile.contacts.instagam}
                    </li>
                    <li>
                        youtube: {profile.contacts.youtube}
                    </li>
                    <li>
                        github: {profile.contacts.github}
                    </li>
                    <li>
                        mainLink: {profile.contacts.mainLink}
                    </li>
                </ul>
                <div>lookingForAJob: {profile.lookingForAJob}</div>
                <div>lookingForAJobDescription: {profile.lookingForAJobDescription}</div>
            </div>
        );
    }
}

export default Avatar;